.modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #1c1c21;
    border: 1px solid rgba(76, 76, 255, 0.2);
    box-sizing: border-box;
    box-shadow: 0 24px 32px rgba(0, 0, 0, 0.04), 0 16px 24px rgba(0, 0, 0, 0.04), 0 4px 8px rgba(0, 0, 0, 0.04), 0 0 1px rgba(0, 0, 0, 0.04);
    border-radius: 40px;
    z-index: 2;
    min-width: 720px;
}

.modal__closeIcon {
    composes: mt-15 mr-10 from global;
    position: absolute;
    top: 0;
    right: 0;
}

.modal__closeIcon:hover {
    cursor: pointer;
    opacity: .7;
}